html{
  text-rendering: optimizeLegibility;
}
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(hsla(0,0%,0%,0.7),hsla(0,0%,0%,0.7)),url(assets/image/bamboo3_tile.jpg);
  background-size: cover;
  background-position: center;
  height:100%;
  /* padding-bottom:25px; */
}
.homepage_layout{
  margin-top: 30px;
  margin-bottom: 30px;
}
/*---------------------------------------------CARD--------------------------------------------*/
.card_margin{
  margin: 0px 0px;
}
.card-body{
  width: 100%;  
  height:100%;
  opacity: 0;  
  position: absolute;
  background-color: rgba(255, 255, 255, 0.9);
  transition: opacity 0.7s;
}
.card_margin:hover .card-body{
  opacity: 1;
}
.card_loader{
  height: 180px;
  width:100%;
  background-color: darkgray;
}
.card_loader_text{
  position: absolute;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
}
/*--------------------------------------------BUTTON-------------------------------------------*/
.back-button-container{
  display:flex;
  text-align: center;
  justify-content: center;
}
.back-button{
  margin-top:40px;
  width:70px;
  height: 25px;
  background-color: white;
  cursor: pointer;
  align-items: center;
}
/*---------------------------------------------SLIDER------------------------------------------*/
.carousal-bg{
  background-color: rgba(200, 200, 200, 0.3);
  height: 100%;
  flex: 1;
}

.carousel-indicators {
  position: absolute;
  color:chartreuse;
  top: 430px;
}

.carousel-inner{
  /* top: 20px; */
}

.slider-image{
  /* filter: brightness(50%); */
  /* max-height: "400px";
  height:390px; */

  object-fit: contain;
  width: 100%;
  max-height: 420px;
  
}
.loader{
  width:100%;
  height: 500px;
  background-color: rgb(168, 173, 163);
  font-size: 400%;
  font-weight: 300;
}
.loading_text{
  position: absolute;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
}
/*--------------------------------------------------------------------------------------------*/
.hide{
  display:none;
}
/*---------------------------------contact page-----------------------------------------------*/
h3:after{
    display: block;
    height: 2px;
    background-color: #fff;
    content: " ";
    width: 100px;
    margin: 0 auto;
    margin-top: 5px;
}
.contact{
  padding: 30px 0;
  color:#fff;
  margin-bottom: 50px;
}
.contact_details{
 text-align: center;
 margin: 25px;
}




.logo {
  max-width: 100px;
  height: auto;
}

/** AAbout page ***/

.aboutbg{
  background-image: url('./assets/image/Portfolio_About.jpg');
  width: 100%;
  height: 100%;
  min-height: 520px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}


.aboutText{
  position:absolute;
  left: 50%;
  width: 40%;
  padding-top: 30px;
  height: auto;
}

.aboutText p {
  color: #fff;
  font-family: 'Arial';
} 

.red {
  color:rgba(185, 2, 2, 0.767);
}


.abs-center {
  display: flex;
  justify-content: center;
  align-items: center; 
}

.cn {
  position: absolute;
  left: 100px;
  top: 100px;
  text-align: center;
}

.inner {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 200px;
  height: auto;
  -webkit-transform: translate(-50%, -50%);  
  transform: translate(-50%, -50%);   
}

.footer{
  background-color: azure;
  height:25px;
  font-size: small;
  position:relative;
  width: 100%;
  bottom:calc(100% - 25px);
}

.container-main {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.blog-container{
  margin-top: 20px
}